@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    background-color: royalblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  justify-content: space-evenly;

  padding: 20px;
  /* height: 100vh;
  width: 100vw; */
}

.app__headingRight {
  color: #3b4a8d;
  text-align: center;
  margin: 10px;
  background-color: #ffffff;
  padding: 10px;
  border-radius: 10px;
}

.app__dropdown {
  background-color: rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  border-radius: 10px;
  outline: none;
  border: none;
  border-radius: 15px;
  color: white;
  font-weight: bolder;
  margin: 10px;
}

.app__header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app__header > h1 {
  color: #eeeeee;
  font-size: 2rem;
  padding: 20px;
  border-radius: 10px;
  background-color: rgb(45, 34, 104, 0.4);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
}

.app__stats {
  display: flex;
  justify-content: space-between;
}

.app__left {
  flex: 0.7 1;
}

.app__information {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
}

.app__information > h3 {
  color: rgb(45, 34, 104);
  font-weight: 800;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.app__information > h3:last-of-type {
  margin-top: 1rem;
}

.app__right {
  flex: 0.3 1;
  width: 40%;
  margin: 25px;
  color: #313131;
  background-color: rgba(255, 255, 255, 0);
}

.app__rightTable {
  padding: 20px;
  border-radius: 15px;
  background-color: rgb(45, 34, 104, 0.4);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
}

@media (max-width: 1190px) {
  .app {
    flex-direction: column;
  }
  .app__right {
    width: 90%;
  }
}

@media (max-width: 600px) {
  .app__header > h1 {
    font-size: 1.4rem;
    padding: 10px;
  }
  .app__stats {
    flex-direction: column;
  }
  .app__right {
    margin: 0px;
    width: 100%;
  }
}

p {
  color: black;
  font-weight: 500;
}

.infoBox {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  margin: 10px;
  flex: 1 1;
  cursor: pointer;
  border-radius: 15px;
  background-color: rgba(105, 137, 224, 0.31);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  margin-bottom: 5px;
  padding: 10px;
  box-shadow: 10px 10px 10px rgba(48, 48, 48, 0.51);
}

.infoBox__heading {
  color: white;
}
.cases {
  background-color: #9059ff;
}
.recovered {
  background-color: #4cd97b;
}
.deaths {
  background-color: #ff5959;
}

.infoBox:not(:last-child) {
  margin-right: 10px;
}

.infoBox--selected {
  border-top: 15px solid greenyellow;
}

.infoBox--red {
  border-color: rgb(255, 44, 44);
}

.infoBox__cases--green {
  /* color: lightgreen; */
}

.infoBox__cases {
  /* color: #cc1034; */
  font-weight: 900;
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.infoBox__total {
  color: #000000;

  font-weight: 700;
  font-size: 1rem;
  margin-top: 15px;
}

.table {
  margin-top: 20px;
  overflow: scroll;
  /* color: #6a5d5d; */
  background-color: rgba(#9059ff, 0.5);
  /* box-shadow: 0 0 8px rgba(0, 0, 0, 0.5); */
  height: 400px;
  font-size: 1.5rem;
  font-weight: 500;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
  border: none;
}

.table tr:nth-of-type(odd) {
  background-color: rgba(100, 100, 100, 0.411);
}

.map {
  height: 500px;
  background-color: rgba(#9059ff, 0.5);
  padding: 1rem;
  border-radius: 20px;
  margin: 16px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
  height: 100%;
  border-radius: 12px;
}

.info-flag img {
  width: 100px;
  border-radius: 5px;
}

.info-name {
  font-size: 20px;
  font-weight: bold;
  color: #555;
}

.info-container {
  width: 150px;
}

.info-flag {
  height: 80px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
}

.info-confirmed,
.info-recovered,
.info-deaths {
  font-size: 16px;
  margin-top: 5px;
}

@media (max-width: 600px) {
  .map {
    height: 300px;
    background-color: rgba(#9059ff, 0.5);
    padding: 5px;
    border-radius: 20px;
    margin: 15px 0px;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
  }
}

.home{height:100vh;width:100vw;display:flex;align-items:center;justify-content:center;flex-direction:column;background-color:royalblue}.home h1{font-size:calc(3vh + 3vw);font-weight:800;color:#fff;text-align:center;margin:0;margin-bottom:calc(.5vh + .5vw)}.home h4{font-size:calc(1vh + 1vw);font-weight:500;color:#fff;text-align:center;margin:0;margin-bottom:calc(.5vh + .5vw)}.home .options{height:80vh;width:80vw;display:flex;align-items:center;justify-content:space-around;flex-direction:column}.home .options .option{text-decoration:none;display:flex;justify-content:center;align-items:center;width:calc(10vw + 10vh);height:calc(10vh + 10vw);font-size:calc(1.5vh + 1.5vw);padding:calc(1vh + 1vw);font-weight:800;color:#fff;cursor:pointer;margin:20px;text-align:center;border:none;background-size:300% 100%;border-radius:calc(1vh + 1vw);transition:all .4s ease-in-out;background-image:linear-gradient(to right, #25aae1, #40e495, #30dd8a, #2bb673);box-shadow:0 4px 15px 0 rgba(49,196,190,0.75)}.home .options .option:hover{background-position:100% 0;transition:all .4s ease-in-out}.home .options .option:focus{outline:none}

